.completed-page {
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  span {
    font-size: 25px;
    color: #000;
    font-weight: 400;
    line-height: 25px;
  }

  img {
    height: 500px;
    width: auto;
  }

  button {
    width: auto;
    padding: 0 20px;
    margin: 20px 0 0 0;
  }
}