.landing-page {
  width: 100vw;
  height: 100vh;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: flex-end;
  position: relative;

  img {
    height: auto;
    width: 50%;
  }

  .input-row-wrapper {
    position: absolute;
    right: calc(50% - 150px);
    top: 10%;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;

    .logo {
      width: 300px;
      height: auto;
      margin: 0 0 30% 0;
    }

    .input-row {
      width: 300px;
      display: flex;
      flex-direction: row;
      justify-content: flex-start;
      align-items: center;
      margin: 10px 0;
      padding: 0 40px 0 0;

      .label {
        width: 60px;
        font-size: 14px;
        color: #000;
        font-weight: 400;
        line-height: 25px;
      }

      input,
      select {
        height: 30px;
        flex: 1 1 auto;
        font-size: 14px;
        color: #000;
        font-weight: 400;
        line-height: 25px;
        padding: 0 0 0 10px;
        border: 1px solid #000;
        border-radius: 10px;
      }
    }

    button {
      margin: 30px 0 0 0;
    }
  }
}