* {
  box-sizing: border-box;
  font-family: 'Open Sans', 'PingFang TC', '微軟正黑體', sans-serif;
}

html, body {
  padding: 0;
  font-size: 16px;
  margin: 0;
  background-color: #FFF;
}

body {
  position: relative;
}

h1, h2, h3, h4, h5, p, div {
  margin: 0;
}

button {
  width: 110px;
  height: 40px;
  font-size: 20px;
  color: #FFF;
  font-weight: 600;
  line-height: 40px;
  background-color: #006AA6;
  border: none;
  border-radius: 30px;
  outline: none;
  cursor: pointer;
  padding: 0;
  margin: 0;

  &:hover {
    background-color: #10324C;
  }
}