.image-set-choose-page {
  padding: 50px 0 0 0;
  width: 100vw;
  height: 100vh;
  position: relative;
  display: grid;
  grid-template-columns: 1fr 1fr;
  column-gap: 30px;
  background-color: #CCC;

  .header {
    width: 100%;
    height: 50px;
    position: absolute;
    background-color: #333;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    span {
      font-size: 14px;
      color: #FFF;
      font-weight: 400;
      line-height: 25px;
    }
  }

  .click-block-wrapper {
    width: 100%;
    height: 100%;
    background-color: #FFF;
    overflow: hidden;

    .click-block-expander {
      width: 100%;
      height: 100%;
      transition: all 0.2s ease;

      &:hover {
        transform: scale(1.05);
      }

      .click-block {
        width: 100%;
        height: 100%;
        background-size: contain;
        background-position: center;
        background-repeat: no-repeat;
        background-color: transparent;
        cursor: pointer;
        transition: all 0.3s ease;
      }
    }
  }
}